import React, { useState } from 'react';
import axios from 'axios';
import { Link, Redirect, useHistory } from 'react-router-dom';
import { authenticate, isAuth } from '../../auth/helper.jsx'
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.min.css'

function LoginForm() {
  const history = useHistory();
  const [values, setValues] = useState({
    email: "",
    password: "",
    buttonText: "Login",
  });

  const { email, password, buttonText } = values;

  const handleChange = (name) => (event) => {
    setValues({
      ...values,
      [name]: event.target.value,
    });
  };

  // const clickSubmit = (event) => {
  //   event.preventDefault();
  //   console.log(email, password);
  //   setValues({ ...values, buttonText: "Verifing" });
  //   setValues({
  //     ...values,
  //     email: "",
  //     password: "",
  //     buttonText: "Verified",
  //   });

  //   if (email === "admin" && password === "admin") {
  //     history.push("/");
  //   } else {
  //     alert('username password does not matched')
  //   }
  // };

  const clickSubmit = (event) => {
    //
    event.preventDefault();
    setValues({ ...values, buttonText: 'Verifing' })
    axios({
        method: 'POST',
        url: `${process.env.REACT_APP_API}/signin`,
        data: { email, password }
    })
        .then(response => {
            console.log('Signin Success', response)

            // save the response (user , token) localStorage and cookies 
            authenticate(response, () => {
                setValues({ ...values, email: '', password: '', buttonText: 'Verified' })
                // toast.success(`Hey ${response.data.user.name}, Welcome back!`)
                if (isAuth() && isAuth().role === 'admin') { 
                    history.push('/admin') 
                } else if (isAuth() && isAuth().role === 'client'){ 
                    history.push('/client') 
                } else if (isAuth() && isAuth().role === 'subadmin'){ 
                  history.push('/sub-admin') 
              } else {
                    history.push('/vendor') 
                }
            })
        })
        .catch(error => {
          console.log('Signin error', error.response.data)
          setValues({ ...values, buttonText: 'Submit' })
          toast.error("Login Failed")
        })
}

  const signinForm = () => (
    <form className="uk-form-stacked">
    <h1 className="uk-h2 uk-margin-medium-bottom">Sign in to Dehnovo</h1>
    <div
      className="uk-text-lead uk-text-danger"
      style={{ display: "none" }}
    ></div>
    <div className="uk-margin">
      <label htmlFor="user" className="uk-form-label">
        Email or Username
      </label>
      <input
        id="user"
        type="text"
        name="username"
        className="uk-input uk-form-large"
        onChange={handleChange("email")}
        value={email}
      />
    </div>
    <div className="uk-margin">
      <div className="uk-form-label uk-flex uk-flex-between">
        <div className="uk-flex uk-flex-between">
          <label htmlFor="password" className="uk-">
            Password
          </label>
          <a className="uk-link-text">Forgot Password?</a>
        </div>
      </div>
      <input
        id="password"
        type="password"
        name="password"
        className="uk-input uk-form-large"
        onChange={handleChange("password")}
        value={password}
      />
    </div>
    <div className="uk-margin-medium-top uk-text-center">
      <button
        type="submit"
        className="uk-button uk-button-primary uk-button-large"
        onClick={clickSubmit}
      >
        {buttonText}
      </button>
    </div>
    <p className="uk-text-center">
      No account yet?
      <Link to="/signup"> Sign up now</Link>
    </p>
  </form>

  )

  return (
    <React.Fragment>
      <div className="uk-section uk-section-default uk-section-large">
        <div className="uk-container uk-container-small">
          <div id="login" className="uk-width-large uk-margin-auto">
          {isAuth() ? <Redirect to="/" /> : null}
          {signinForm()}
          </div>
        </div>
      </div>
    </React.Fragment>
  );
}

export default LoginForm;
