import React, { useState } from "react";
import axios from "axios";
import { Link, Redirect } from "react-router-dom";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.min.css";
import { isAuth } from "../../auth/helper.jsx";
function SignupForm() {
  const [values, setValues] = useState({
    name: "",
    email: "",
    password: "",
    role: "",
    mobile: "",
    company : "",
    address: "",
    buttonText: "Signup",
  });

  const { name, email, password, role, mobile, company, address, buttonText } = values;

  const handleChange = (name) => (event) => {
    // console.log(event.target.value);
    setValues({ ...values, [name]: event.target.value });
  };

  const clickSubmit = (event) => {
    event.preventDefault();
    setValues({ ...values, buttonText: "Verifing" });
    axios({
      method: "POST",
      url: `${process.env.REACT_APP_API}/signup`,
      data: { name, email, password, role, mobile, company, address },
    })
      // console.log(values)
      .then((response) => {
        console.log("Signup Success", response);
        setValues({
          ...values,
          name: "",
          email: "",
          password: "",
          role: "",
          mobile: "",
          buttonText: "Varified",
        });
        toast.success(response.data.message);
      })
      .catch((error) => {
        console.log("Signup error", error.response.data);
        setValues({ ...values, buttonText: "Signup" });
        toast.error(error.response.data.error);
      });
  };

  const signupForm = () => (
    <form action="#" method="post" className="uk-form-stacked " uk-grid="true">
      <h1 className="uk-h2 uk-width-1-1 uk-margin-medium-bottom">Signup with Dehnovo</h1>
      <div
        className="uk-text-lead uk-text-danger"
        style={{ display: "none" }}
      ></div>
      <div className="uk-margin uk-width-1-2@s">
        <label for="user" className="uk-form-label">
          Name
        </label>
        <input
          id="user"
          type="text"
          name="name"
          onChange={handleChange("name")}
          value={name}
          className="uk-input"
          placeholder="Enter Name"
        />
      </div>
      <div className="uk-margin uk-width-1-2@s">
        <label for="user" className="uk-form-label">
          Email
        </label>
        <input
          id="user"
          type="text"
          name="username"
          className="uk-input"
          placeholder="Enter Email"
          onChange={handleChange("email")}
          value={email}
        />
      </div>
      <div className="uk-margin uk-width-1-2@s">
        <div className="uk-form-label uk-flex uk-flex-between">
          <div className="uk-flex uk-flex-between">
            <label for="password" className="uk-">
              Password
            </label>
          </div>
        </div>
        <input
          id="password"
          type="password"
          name="password"
          autocomplete="current-password"
          className="uk-input"
          placeholder="Type Password"
          onChange={handleChange("password")}
          value={password}
        />
      </div>
      <div className="uk-margin uk-width-1-2@s">
        <label for="user" className="uk-form-label">
          Mobile
        </label>
        <input
          id="user"
          type="text"
          name="mobile"
          className="uk-input"
          placeholder="Enter Mobile"
          onChange={handleChange("mobile")}
          value={mobile}
        />
      </div>
      <div className="uk-margin uk-width-1-1">
        <label for="company" className="uk-form-label">
          Compnay Name
        </label>
        <input
          id="company"
          type="text"
          name="company"
          className="uk-input"
          placeholder="Enter Compnay Name"
          onChange={handleChange("company")}
          value={company}
        />
      </div>
      <div className="uk-margin uk-width-1-1">
        <label for="address" className="uk-form-label">
          Address
        </label>
        <textarea
          id="address"
          type="text"
          name="address"
          rows="3"
          className="uk-textarea"
          placeholder="Enter Address"
          onChange={handleChange("address")}
          value={address}
        ></textarea>
      </div>
      
      <div className="uk-margin uk-grid-small uk-child-width-auto uk-grid">
        <label>
          <input
            className="uk-radio"
            type="radio"
            onChange={handleChange("role")}
            value={`client`}
            checked={role === "client"}
            name="role"
          />{" "}
          Client
        </label>
        <label>
          <input
            className="uk-radio"
            type="radio"
            onChange={handleChange("role")}
            value={`vendor`}
            checked={role === "vendor"}
            name="role"
          />{" "}
          Vendor
        </label>
      </div>

      <div className="uk-margin-medium-top uk-text-center">
        <button
          type="submit"
          className="uk-button uk-button-primary"
          onClick={clickSubmit}
        >
          {buttonText}
        </button>
      </div>
      <p className="uk-text-center">
        I have an account?
        <Link to="/login"> Sign In</Link>
      </p>
    </form>
  );
  return (
    <React.Fragment>
      <div className="uk-section uk-section-default">
      <ToastContainer />
        <div className="uk-container uk-container-small">
          <div id="login" className="uk-margin-auto">
            {signupForm()}
          </div>
        </div>
      </div>
    </React.Fragment>
  );
}

export default SignupForm;
