import React, { useState } from "react";
import Layout from "../../layouts/Layout";
import requirement from "../requirement.jpg";
import { Link } from "react-router-dom";

function RequirementPost() {
  return (
    <Layout>
      <div className="uk-container uk-margin">
        <h2 className="uk-text-bold uk-text-center">Post Your Requirement</h2>
        <div className="uk-child-width-1-2@m" uk-grid="true">
          <div uk-sticky="sel-target: .uk-navbar-container; cls-active: uk-navbar-sticky; offset: 100">
            <img src={requirement} alt="requirement" className="uk-visible@m" />
          </div>
          <div>
            <form action="/" className="uk-form-stacked">
              <div
                className="uk-text-lead uk-text-danger"
                style={{ display: "none" }}
              ></div>
              <div className="uk-margin">
                <label for="user" className="uk-form-label">
                  Title
                </label>
                <input
                  id="user"
                  type="text"
                  name="name"
                  className="uk-input"
                  placeholder="Enter Title"
                />
              </div>
              <div class="uk-margin">
                <label class="uk-form-label" for="form-stacked-select">
                  Category
                </label>
                <div class="uk-form-controls">
                  <select class="uk-select" id="form-stacked-select">
                    <option>IT hardware</option>
                    <option>It Software</option>
                  </select>
                </div>
              </div>
              <div class="uk-margin">
                <label class="uk-form-label" for="form-stacked-select">
                  Products
                </label>
                <div class="uk-form-controls">
                  <select class="uk-select" id="form-stacked-select">
                    <option>IT hardware</option>
                    <option>It Software</option>
                  </select>
                </div>
              </div>

              <div className="uk-margin">
                <label for="user" className="uk-form-label">
                  Image
                </label>
                <input
                  id="image"
                  type="file"
                  name="image"
                  className="uk-input"
                  placeholder="Enter Image"
                />
              </div>
              <div class="uk-margin">
                <textarea
                  class="uk-textarea"
                  rows="5"
                  placeholder="Enter description"
                ></textarea>
              </div>

              <div class="uk-margin">
                <textarea
                  class="uk-textarea"
                  rows="5"
                  placeholder="Additional Information about product"
                ></textarea>
              </div>
              <div className="uk-margin-medium-top uk-text-center uk-display-block">
                <button
                  type="submit"
                  className="uk-button uk-button-primary "
                  style={{ width: "100%" }}
                >
                  Post
                </button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </Layout>
  );
}

export default RequirementPost;
