import React, { useState, useEffect } from "react";
import axios from "axios";
// import { Link, Redirect } from 'react-router-dom';
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.min.css";
import Layout from "../layouts/Layout";

import { isAuth, getCookie, signout, updateUser } from "../auth/helper";

function RegisteredVendor({ history }) {
  const token = getCookie("token");

  const [values, setValues] = useState([])

  useEffect(() => {
    loadProfile();
  }, []);

  const loadProfile = () => {
    axios({
      method: "GET",
      url: `${process.env.REACT_APP_API}/vendors`,
      headers: {
        Authorization: `Bearer ${token}`,
      },
    })
      .then((response) => {
        console.log("Private Profile update", response.data);
        const value  = response.data;

        const data = [];
        for ( const val of Object.entries(value)) {
          data.push(val)
        }
        setValues(data)
        
      })
      .catch((error) => {
        console.log("Profile Update error", error);
        if (error.response.status === 401) {
          signout(() => {
            history.push("/");
          });
        }
      });
  };

  return (
    <Layout>
      <div className="uk-container uk-section">
        <div className="uk-margin">
          <h1 className="uk-margin-remove">Registered Vendors</h1>
          <p className="uk-margin-remove">Find all registered vendor here</p>
        </div>
      
        {/* {JSON.stringify(values)} */}
        
        <table className="uk-table uk-table-divider">
          <thead>
            <tr>
              <th>Vendor Name</th>
              <th>Company Name</th>
              <th>Location</th>
            </tr>
          </thead>
          <tbody>
          {values.map(vendor => (
     
            
            <tr  key={vendor[1]._id} id={vendor[1]._id}>
              <td>{vendor[1].name}</td>
              <td>{vendor[1].company}</td>
              <td>{vendor[1].address}</td>
            </tr>
            ))}
            
          </tbody>
        </table>
        
      </div>
    </Layout>
  );
}

export default RegisteredVendor;
