import React from 'react';
import { Route, Redirect } from 'react-router-dom';
import { isAuth } from './helper.jsx';

const ClientRoute = ({ component: Component, ...rest }) => (
    <Route {...rest} render={
        props => isAuth() &&  isAuth().role === 'client' ? <Component {...props} /> : <Redirect to={{ pathname: '/login', state: { from: props.location } }} />
    }></Route>
)

export default ClientRoute;