import React, { useState, useEffect } from "react";
import axios from "axios";
// import { Link, Redirect } from 'react-router-dom';
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.min.css";
import Layout from "../layouts/Layout";
import { isAuth, getCookie, signout, updateUser } from "../auth/helper";
import { Link, useHistory } from "react-router-dom";

function EditProfile() {
    const history = useHistory();
  const [values, setValues] = useState({
    name: "",
    email: "",
    password: "",
    // role: "",
    mobile: "",
    company: "",
    address: "",
    buttonText: "Edit",
  });

  const token = getCookie("token");

  useEffect(() => {
    loadProfile();
  }, []);

  const loadProfile = () => {
    axios({
      method: "GET",
      url: `${process.env.REACT_APP_API}/user/${isAuth()._id}`,
      headers: {
        Authorization: `Bearer ${token}`,
      },
    })
      .then((response) => {
        console.log("Private Profile update", response);
        const {
          name,
          email,
          mobile,
          company,
          address,
          password,
        } = response.data;
        setValues({
          ...values,
          name,
          email,
          mobile,
          company,
          address,
          password,
        });
      })
      .catch((error) => {
        console.log("Profile Update error", error);
        if (error.response.status === 401) {
          signout(() => {
            history.push("/");
          });
        }
      });
  };

  const {
    name,
    email,
    mobile,
    company,
    address,
    password,
    buttonText,
  } = values;

  const handleChange = (name) => (event) => {
    setValues({ ...values, [name]: event.target.value });
  };

  const clickSubmit = (event) => {
    event.preventDefault();
    console.log("sefvesrferf", values);
    setValues({ ...values, buttonText: "Editing" });
    axios({
      method: "put",
      url: `${process.env.REACT_APP_API}/user/update`,
      headers: {
        Authorization: `Bearer ${token}`,
      },
      data: { name, password, email, mobile, company, address },
    })
      .then((response) => {
        console.log("Profile Update Success", response);
        updateUser(response, () => {
          setValues({ ...values, buttonText: "Submitted" });
          toast.success("Profile Updated Successfully");
          history.push("/account");
        });
      })
      .catch((error) => {
        console.log("Profile update error", error.response.data.error);
        setValues({ ...values, buttonText: "Submit" });
        toast.error(error.response.data.error);
      });
  };

  const editProfileForm = () => (
    <form action="#" method="post" className="uk-form-stacked " uk-grid="true">
      <h1 className="uk-h2 uk-width-1-1 uk-margin-medium-bottom">
        Edit Your Profile
      </h1>
      <div
        className="uk-text-lead uk-text-danger"
        style={{ display: "none" }}
      ></div>
      <div className="uk-margin uk-width-1-2@s">
        <label for="user" className="uk-form-label">
          Name
        </label>
        <input
          id="user"
          type="text"
          name="name"
          onChange={handleChange("name")}
          value={name}
          className="uk-input"
          placeholder="Enter Name"
        />
      </div>
      <div className="uk-margin uk-width-1-2@s">
        <label for="user" className="uk-form-label">
          Email
        </label>
        <input
          id="user"
          type="text"
          name="username"
          className="uk-input"
          placeholder="Enter Email"
          onChange={handleChange("email")}
          value={email}
        />
      </div>
      <div className="uk-margin uk-width-1-2@s">
        <div className="uk-form-label uk-flex uk-flex-between">
          <div className="uk-flex uk-flex-between">
            <label for="password" className="uk-">
              Password
            </label>
          </div>
        </div>
        <input
          id="password"
          type="password"
          name="password"
          autocomplete="current-password"
          className="uk-input"
          placeholder="Type Password"
          onChange={handleChange("password")}
          value={password}
        />
      </div>
      <div className="uk-margin uk-width-1-2@s">
        <label for="user" className="uk-form-label">
          Mobile
        </label>
        <input
          id="user"
          type="text"
          name="mobile"
          className="uk-input"
          placeholder="Enter Mobile"
          onChange={handleChange("mobile")}
          value={mobile}
        />
      </div>
      <div className="uk-margin uk-width-1-1">
        <label for="company" className="uk-form-label">
          Compnay Name
        </label>
        <input
          id="company"
          type="text"
          name="company"
          className="uk-input"
          placeholder="Enter Compnay Name"
          onChange={handleChange("company")}
          value={company}
        />
      </div>
      <div className="uk-margin uk-width-1-1">
        <label for="address" className="uk-form-label">
          Address
        </label>
        <textarea
          id="address"
          type="text"
          name="address"
          rows="3"
          className="uk-textarea"
          placeholder="Enter Address"
          onChange={handleChange("address")}
          value={address}
        ></textarea>
      </div>

      <div className="uk-margin-medium-top uk-text-center">
        <button
          type="submit"
          className="uk-button uk-button-primary"
          onClick={clickSubmit}
        >
          {buttonText}
        </button>
      </div>
      <p className="uk-text-center">
        <Link to="/account"> Back</Link>
      </p>
    </form>
  );
  return (
    <Layout>
      <div className="uk-section uk-section-default">
        <ToastContainer />
        <div className="uk-container uk-container-small">
          <div id="login" className="uk-margin-auto">
            {editProfileForm()}
          </div>
        </div>
      </div>
    </Layout>
  );
}

export default EditProfile;
