import React, { useState, useEffect } from "react";
import axios from "axios";
// import { Link, Redirect } from 'react-router-dom';
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.min.css";
import Layout from "../layouts/Layout";
import { isAuth, getCookie, signout, updateUser } from "../auth/helper";
import { useHistory, Link } from "react-router-dom";
import Breadcrumb from "../components/Breadcrumb";

function Account() {
  const history = useHistory();

  const [values, setValues] = useState({
    role: "",
    name: "",
    email: "",
    address: "",
    mobile: "",
    company: "",
    password: "",
  });

  const token = getCookie("token");

  useEffect(() => {
    loadProfile();
  }, []);

  const loadProfile = () => {
    axios({
      method: "GET",
      url: `${process.env.REACT_APP_API}/user/${isAuth()._id}`,
      headers: {
        Authorization: `Bearer ${token}`,
      },
    })
      .then((response) => {
        console.log("User Profile", response);
        const { role, name, email, address, mobile, company } = response.data;
        setValues({ ...values, role, name, email, address, mobile, company });
      })
      .catch((error) => {
        console.log("Profile view error", error);
        if (error.response.status === 401) {
          signout(() => {
            history.push("/");
          });
        }
      });
  };

  return (
    <Layout>
      <div className="uk-margin uk-text-center">
        <h1 className="uk-margin-remove">Your Profile</h1>
        <p className="uk-margin-remove">Update your details here</p>
      </div>
      <div className="uk-container">
        <div uk-grid="true">
          <div className="uk-width-1-4@m uk-visible@m">
            <ul className="uk-list uk-list-divider">
              <li>
                <Link to="/account">Account</Link>
              </li>
              {isAuth() && isAuth().role === "client" && (
                <li>
                  <Link to="/requirement-list">Your Posts</Link>
                </li>
              )}
              {isAuth() && isAuth().role === "vendor" && (
                <li>
                  <Link to="/chat">Applied Post</Link>
                </li>
              )}
              <li>
                <Link to="/">
                  <span
                    // style={{ cursor: "pointer", color: "white" }}
                    onClick={() => {
                      signout(() => {
                        history.push("/");
                      });
                    }}
                  >
                    Logout
                  </span>
                </Link>
              </li>
            </ul>
          </div>
          <div className="uk-width-expand">
            <h2 className="uk-text-bold">{values.name}
            <Link to="/edit-profile" className="uk-float-right uk-button-small uk-button-danger">Edit</Link>
            </h2>
            <hr />
            <table class="uk-table">
              <tbody>
                <tr>
                  <th>Address</th>
                  <td>{values.address}</td>
                </tr>
                <tr>
                  <th>Compnay Name</th>
                  <td>{values.company}</td>
                </tr>
                <tr>
                  <th>Email</th>
                  <td>{values.email}</td>
                </tr>
                <tr>
                  <th>Mobile</th>
                  <td>{values.mobile}</td>
                </tr>
                <tr>
                  <th>Role</th>
                  <td>{values.role}</td>
                </tr>

              </tbody>
            </table>
          </div>
        </div>
      </div>
    </Layout>
  );
}

export default Account;
