import React from 'react'
import {BrowserRouter, Switch, Route} from 'react-router-dom'
import App from './App.jsx'
import About from './pages/About.jsx'
import ContactUs from './pages/ContactUs.jsx'
import Login from './pages/Login.jsx'
import Signup from './pages/Signup.jsx'
import ProductDetails from './pages/ProductDetails.jsx'
import BrowseRequirements from './pages/BrowseRequirements.jsx'
import RequirementPost from './components/requirementPost/RequirementPost.jsx'
import Chat from './pages/Chat.jsx'
import ChatOne from './pages/ChatOne.jsx'
import Activate from './auth/Activate.jsx';
import RegisteredVendor from './pages/RegisteredVendor.jsx'
import Account from './pages/Account.jsx'
import EditProfile from './pages/EditProfile.jsx'
import ClientRoute from './auth/ClientRoute.jsx'
import AuthRoute from './auth/AuthRoute.jsx'
import Requirements from './pages/Requirements.jsx'

function Routes() {
    return (
        <BrowserRouter>
            <Switch>
                <Route path="/" exact component={App} />
                <Route path="/about-us" exact component={About} />
                <Route path="/contact-us" exact component={ContactUs} />
                <Route path="/login" exact component={Login} />
                <Route path="/signup" exact component={Signup} />
                <AuthRoute path="/account" exact component={Account} />
                <AuthRoute path="/edit-profile" exact component={EditProfile} />
                <Route path="/requirements/:id" exact component={ProductDetails} />
                <ClientRoute path="/requirement-list" exact component={BrowseRequirements} />
                <AuthRoute path="/requirements" exact component={Requirements} />
                <Route path="/registered-vendors" exact component={RegisteredVendor} />
                <ClientRoute path="/post" exact component={RequirementPost} />
                <AuthRoute path="/chat" exact component={Chat} />
                <AuthRoute path="/chat/:id" exact component={ChatOne} />
                <Route path="/auth/activate/:token" component={Activate} />
                <Route path="/" component={App} />
            </Switch>
        </BrowserRouter>
    )
}

export default Routes













