import React from "react";
import header from './header.jpg'


function Breadcrumb({image, name}) {
  return (
    <React.Fragment>
      <div
        className="uk-position-relative uk-slideshow"
        tabIndex="-1"
        uk-slideshow="ratio: 14:3;"
      >
        <ul className="uk-slideshow-items" style={{minHeight: 289.071}}>
          <li
            tabIndex="-1"
            className="uk-active uk-transition-active"
          >
            <img
              className="slider-img uk-cover"
              src={image ? image : header}
              alt=""
              uk-cover=""
              style={{height: 714, width: 1350}}
            />
          </li>
        </ul>
      </div>
    </React.Fragment>
  );
}

export default Breadcrumb;
