import React, { useState } from "react";
import axios from "axios";
import { Link, Redirect } from "react-router-dom";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.min.css";

function ContactForm() {
  const [values, setValues] = useState({
    name: "",
    email: "",
    mobile: "",
    msg: "",
    buttonText: "Submit",
  });

  const { name, email, mobile, msg, buttonText } = values;

  const handleChange = (name) => (event) => {
    // console.log(event.target.value);
    setValues({ ...values, [name]: event.target.value });
  };

  const clickSubmit = (event) => {
    event.preventDefault();
    setValues({ ...values, buttonText: "sending" });
    axios({
      method: "POST",
      url: `${process.env.REACT_APP_API}/contact`,
      data: { name, email, mobile, msg },
    })
      // console.log(values)
      .then((response) => {
        console.log("Form Submitted", response);
        setValues({
          ...values,
          name: "",
          email: "",
          mobile: "",
          msg: "",
          buttonText: "Done",
        });
        toast.success(response.data.message);
      })
      .catch((error) => {
        console.log("Contact error", error.response.data);
        setValues({ ...values, buttonText: "Submit" });
        toast.error(error.response.data.error);
      });
  };

  return (
    <div>
      <ToastContainer />
      <form>
        <fieldset className="uk-fieldset">
          <div className="uk-margin">
            <input
              className="uk-input"
              type="text"
              placeholder="Enter Name"
              onChange={handleChange("name")}
              value={name}
            />
          </div>
          <div className="uk-margin">
            <input
              className="uk-input"
              type="email"
              autoComplete="email"
              placeholder="Enter Email"
              onChange={handleChange("email")}
              value={email}
            />
          </div>
          <div className="uk-margin">
            <input
              className="uk-input"
              type="Mobile"
              placeholder="Enter Mobile Number"
              onChange={handleChange("mobile")}
              value={mobile}
              maxLength="10"
              minLength="10"
            />
          </div>
          <div className="uk-margin">
            <textarea
              className="uk-textarea"
              rows="5"
              placeholder="You Message"
              onChange={handleChange("msg")}
              value={msg}
            ></textarea>
          </div>
        </fieldset>
        <button className="uk-button uk-button-primary" onClick={clickSubmit}>
          Submit
        </button>
      </form>
    </div>
  );
}

export default ContactForm;
