import React from "react";
import ChatList from "../components/chat/ChatList";
import Layout from "../layouts/Layout";

function Chat() {
  return (
    <Layout>
      <div uk-grid="true">
        <div className="uk-width-1-4@s"  style={{minWidth: 300}}>
          <ChatList />
        </div>
        <div className="uk-width-expand uk-padding-remove uk-visible@s">
          <nav class="uk-navbar-container uk-padding-small" uk-navbar="true">
            user
          </nav>
        </div>
      </div>
    </Layout>
  );
}

export default Chat;
