import React, { useState, useEffect } from "react";
import axios from "axios";
import Layout from "../layouts/Layout";
import ProductCard from "../components/home/ProductCard";
import { isAuth, getCookie, signout, updateUser } from "../auth/helper";

function BrowseRequirements() {

  const [values, setValues] = useState([]);

  useEffect(() => {
    loadPosts();
  }, []);

  console.log(isAuth()._id);

  const loadPosts = () => {
    axios({
      method: "GET",
      url: `${process.env.REACT_APP_API}/post/user/${isAuth()._id}`,
    })
      .then((response) => {
        console.log("Posts", response.data);
        const value = response.data;

        const data = [];
        for (const val of Object.entries(value)) {
          data.push(val);
        }
        setValues(data);
      })
      .catch((error) => {
        console.log("Post error", error);
        if (error.response.status === 401) {
          console.log("error while retriving post");
        }
      });
  };

  return (

    
    <Layout>
      <div className="uk-container uk-section">
          <div className="uk-margin">
          <h1 className="uk-margin-remove">Requirements</h1>
          <p className="uk-margin-remove">Find all requirements here</p>
          </div>
        <div
          className="uk-child-width-1-2 uk-child-width-1-3@s uk-child-width-1-4@m"
          uk-grid="true"
        >
          {values.map((post) => (
            <ProductCard
              key={post[1]._id}
              id={post[1]._id}
              image={`${process.env.REACT_APP_API}/post/photo/${post[1]._id}`}
              title={post[1].title}
              description={post[1].description}
              postedBy={post[1].postedBy}
              chat="false"
            />
          ))}
          
        </div>
      </div>
    </Layout>
  );
}

export default BrowseRequirements;
