import React from "react";
import meeting from "../meet.jpg";
import laptop from "../why.jpg";

function AboutComponent() {
  return (
    <React.Fragment>
      <div className="uk-container uk-margin-large">
        <h1 className="uk-text-center uk-text-bold">About Us</h1>
        <div
          className="uk-divider-icon uk-width-medium uk-margin-auto uk-scrollspy-inview uk-animation-slide-top-medium"
          uk-scrollspy-classname="true"
        ></div>

        <div className="uk-child-width-1-2@s" uk-grid="">
          <div className="uk-grid-item-match uk-flex-middle uk-width-expand@m uk-first-column">
            <div className="uk-panel uk-width-1-1">
              <div
                className="uk-margin uk-scrollspy-inview uk-animation-fade"
                uk-scrollspy-class=""
              >
                <img
                  className="el-image"
                  alt="Dehnovo"
                  data-sizes="(min-width: 920px) 920px"
                  uk-img=""
                  src={laptop}
                  sizes="(min-width: 920px) 920px"
                  srcset={laptop}
                />
              </div>
            </div>
          </div>

          <div className="uk-grid-item-match uk-flex-middle uk-width-expand@m">
            <div className="uk-panel uk-width-1-1">
              <h2
                className="uk-text-left@s uk-text-center uk-scrollspy-inview uk-animation-fade"
                uk-scrollspy-class=""
              >
                Our Story
              </h2>
              <div
                className="uk-margin uk-scrollspy-inview uk-animation-fade"
                uk-scrollspy-class=""
              >
                After more than 2 years in the IT Sales, we decided to alter
                direction. Now, we share our experience to help others. Our ramp
                up process is designed to empower your administration team and
                outfit them with the tools they need to succeed.
                <br />
                We are basically a Business to Business sales platform helping
                the new as well as existing businesses to grow their market by
                the corporate reach. We also help the corporate sector to
                fulfill all their needs at a single platform.
              </div>
            </div>
          </div>
        </div>
        <div className="uk-child-width-1-2@s" uk-grid="">
          <div className="uk-grid-item-match uk-flex-middle uk-width-expand@m">
            <div className="uk-panel uk-width-1-1">
              <h2
                className="uk-text-left@s uk-text-center uk-scrollspy-inview uk-animation-fade"
                uk-scrollspy-class=""
              >
                Our Approach
              </h2>
              <div
                className="uk-margin uk-scrollspy-inview uk-animation-fade"
                uk-scrollspy-class=""
              >
                Our service includes a comprehensive consult to help identify
                gaps and opportunities, a comprehensive report that includes a
                project plan with timelines and milestones, a cost analysis, and
                a schedule. We also offer a suite of quality products that will
                help you get there quickly and smoothly.
              </div>
            </div>
          </div>

          <div className="uk-grid-item-match uk-flex-middle uk-width-expand@m uk-first-column">
            <div className="uk-panel uk-width-1-1">
              <div
                className="uk-margin uk-scrollspy-inview uk-animation-fade"
                uk-scrollspy-class=""
              >
                <img
                  className="el-image"
                  alt="Dehnovo"
                  data-sizes="(min-width: 920px) 920px"
                  data-width="920"
                  data-height="613"
                  uk-img=""
                  src={meeting}
                  sizes="(min-width: 920px) 920px"
                  srcset={meeting}
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    </React.Fragment>
  );
}

export default AboutComponent;
