import React, { useState, useEffect } from 'react';
import axios from 'axios';
import Layout from "../layouts/Layout";
import { Link } from "react-router-dom";

function ProductDetails() {
  const urlId = window.location.pathname.substring(window.location.pathname.lastIndexOf('/') + 1)

  const [values, setValues] = useState({
    title: '',
    category: '',
    product: '',
    description: '',
    additional: '',
    postedBy: '',
});


  useEffect(() => {
    loadPost();
  }, []);

  const loadPost = () => {
    axios({
      method: "GET",
      url: `${process.env.REACT_APP_API}/post/${urlId}`,
    })
      .then((response) => {
        console.log("single post", response.data);
        const { title, category, product, description, additional, postedBy } = response.data;
        setValues({ ...values, title, category, product, description, additional, postedBy })
      })
      .catch((error) => {
        console.log("Post error", error);
        if (error.response.status === 401) {
          console.log("error while retriving post");
        }
      });
  };
  

  return (
    <Layout>
      <div className="uk-container uk-section">
        <div className="uk-child-width-1-2@s" uk-grid="true">
          <div>
            <img
              src={`${process.env.REACT_APP_API}/post/photo/${urlId}`}
              alt="Product Image"
            />
          </div>
          <div>
            <h1 className="uk-text-bold">{values.title}</h1>
            {/* <p>Compnay Name - Bangalore</p> */}
            <div className="uk-margin">
            <Link to={`/chat/${values.postedBy}`} className="uk-link-heading uk-text-bold">
              <span uk-icon="comment"></span> Chat With Client
            </Link>
            </div>
            <div className="uk-text-muted">
              <p>
                <h6>Requirement Description:</h6>
                {values.description}
              </p>
              <p>
              <h6>Additional Description:</h6>
                {values.additional}
              </p>
            </div>
          </div>
        </div>
      </div>
    </Layout>
  );
}

export default ProductDetails;
